import refs from './refs';
import onLibraryBtn from './onLibraryBtn';
import Main from '../main';
const main = new Main();
import { hideError } from './onSubmit';
export default function onHomeBtn() {
  hideError();
  main.init();
  refs.libraryContainer.classList.add('display-none'); // 
  refs.homeContainer.classList.remove('display-none'); //    
  refs.libraryBtn.addEventListener('click', onLibraryBtn); //    
  refs.libraryBtn.classList.remove('current'); //     
  refs.homeBtn.classList.add('current'); //   
  refs.headerEl.classList.remove('header-fon'); //  

  //       library
  if (refs.paginationEl.classList.contains('display-none')) {
    refs.paginationEl.classList.remove('display-none');
  }
}
