import refs from './refs';
import onWatchedBtn from './onWatchedBtn';

export default function onLibraryBtn() {
    refs.homeContainer.classList.add('display-none'); //    
    refs.libraryContainer.classList.remove('display-none'); //  
    refs.libraryBtn.removeEventListener('click', onLibraryBtn); //         
    refs.homeBtn.classList.remove('current'); //     
    refs.libraryBtn.classList.add('current'); //   
    refs.headerEl.classList.add('header-fon'); //   
    
    onWatchedBtn()
    // refs.queueBtn.addEventListener('click', onQueueBtn);
};

