import refs from "./refs";
import onQueueBtn from "./onQueueBtn";
import markupWatchedFilm from "./markupWatchedFilm";

export default function onWatchedBtn() {
    refs.galleryList.innerHTML = '';
    refs.watchedBtn.removeEventListener('click', onWatchedBtn); // 
    refs.queueBtn.addEventListener('click', onQueueBtn); //   
    refs.queueBtn.classList.remove('active') //     
    refs.watchedBtn.classList.add('active'); //     
    refs.paginationEl.classList.add('display-none') //   ,      20
    
    markupWatchedFilm()
}