import refs from "./refs";
import onWatchedBtn from "./onWatchedBtn";
import markupQueueFilm from "./markupQueueFilm";

export default function onQueueBtn() {
    refs.galleryList.innerHTML = '';
    refs.watchedBtn.classList.remove('active') //     
    refs.queueBtn.classList.add('active') //    
    refs.queueBtn.removeEventListener('click', onQueueBtn); //  
    refs.watchedBtn.addEventListener('click', onWatchedBtn); //       
    refs.paginationEl.classList.add('display-none') //   ,      20

    markupQueueFilm()
}

